    export default {
        name: 'app-navigation-icon',
	    data() {
            return {
                width: uni.upx2px(0)+ 'px',
	            height: 0,
            }
	    },
	    props: {
            background: {
				type: String,
				default() {
				    return '#ffffff';
				}
			},
            color: {
				type: String,
				default() {
				    return '#353535';
				}
			},
            columns: {
				type: Number,
				default() {
				    return 3;
				}
			},
            rows: {
				type: Number,
				default() {
				    return 4;
				}
			},
            scroll: {
				type: Boolean,
				default() {
				    return false;
				}
			},
            navs: {
				type: Array,
				default() {
				    return [
                    ]
				}
			}
	    },
	    computed: {
            newData: function() {
                this.width = `${uni.upx2px(750/this.columns)}px`;
                let menus = [];
                let newMenus = [];
                let num = Number(this.columns)* this.rows;
















                if (this.scroll === true) {
                   for (let i = 0; i < Math.ceil(this.navs.length/num); i++) {
                       menus.push(this.navs.slice(i*num, (i+1)*num));
                   }
                   for (let i = 0; i < menus.length;i++) {
                       let arr = [];
                       for (let j = 0; j < Math.ceil(menus[i].length/ Number(this.columns)); j++) {
                           let men = menus[i].slice(j* Number(this.columns), (j+1)* Number(this.columns));
                           arr.push(men);
                       }
                       newMenus.push(arr);
                   }
                } else {
                    if (this.rows === -1) {
                        menus = [this.navs];
                    } else {
                        menus.push(this.navs.slice(0, num));
                    }
                    for (let i = 0; i < menus.length;i++) {
                        let arr = [];
                        for (let j = 0; j < Math.ceil(menus[i].length/ Number(this.columns)); j++) {
                            let men = menus[i].slice(j* Number(this.columns), (j+1)* Number(this.columns));
                            arr.push(men);
                        }
                        newMenus.push(arr);
                    }
               }
                if (newMenus.length === 0 ) return newMenus;
                return newMenus;
            }
	    },
        watch: {
            newData: {
                handler: function(newVal) {
                    this.height = `${uni.upx2px(newVal[0].length * 156)}px`;
                },
                immediate: true
            }
        }
    }
